<template>
	<view class="harvest">
		<view class="title">
			<view class="order-number">订单编号：{{order.OrderNo}}</view>
			<view class="status">{{statusList[order.Status]}}</view>
		</view>
		<view class="middle">
			<view class="middle-left">
				<image :src="order.Image?`https://farm.juzhentech.com/${order.Image}`: ''" mode=""></image>
				<view class="">
					<view class="name">{{order.SeedlingsName}}</view>
					<view class="unit">{{order.Unit}}</view>
				</view>
			</view>
			<view class="middle-number">x{{order.Amount}}</view>
		</view>
		<view class="bottom-btn">
			<view class="cancel" v-if="order.Status===1" @click="this.$refs.popup.open()">查看物流</view>
			<view class="save" @click="handleInformation" v-if="order.Status===1">确认收获</view>
			<view class="save" @click="handleAppraise" v-if="order.Status === 2">去评价</view>
		</view>
		<uni-popup ref="popup" type="center">
			<logistics-dialog :order="{LogisticsCompany: order.LogisticsCompany,LogisticsNo: order.LogisticsNo}"
				@onHide="handleHide">
			</logistics-dialog>
		</uni-popup>
	</view>
</template>

<script>
	import LogisticsDialog from '../../order-information/components/logistics/index.vue'
	export default {
		components: {
			LogisticsDialog
		},
		props: {
			order: {
				default: {}
			}
		},
		data() {
			return {
				statusList: ['待发货', '待收货', '待评价', '已完成']
			}
		},
		methods: {
			handleHide(e) {
				this.$refs.popup.close()
			},
			handleInformation() {
				let items = JSON.stringify(this.order);
				uni.setStorageSync("harvestList", items);
				uni.navigateTo({
					url: '/pages/user-center/harvest-order/order-information/index'
				})
			},
			/**
			 * 去评价
			 */
			handleAppraise() {
				uni.navigateTo({
					url: `/pages/user-center/user-work/appraise/index?type=1&orderNo=${this.order.OrderNo}&orderId=${this.order.OrderID}`
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.harvest {
		background: #fff;
		padding: 20rpx;
		border-radius: 16rpx;
		// margin-left: 20px;
		// margin-right: 20px;
		margin-bottom: 20rpx;
	}

	// .harvest:nth-of-type(1) {
	// 	margin-top: 20px;
	// }

	.title {
		display: flex;
		justify-content: space-between;
		font-weight: 400;
		line-height: 34rpx;
		font-size: 24rpx;
		padding-bottom: 20rpx;
		border-bottom: 2rpx solid #efefef;

		.order-number {
			color: #979DA6;
		}

		.status {
			color: #333333;
		}
	}

	.middle {
		padding: 20rpx 0;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		border-bottom: 2rpx solid #efefef;

		.middle-left {
			display: flex;
			justify-content: flex-start;

			.name {
				font-size: 28rpx;
				font-weight: 500;
				color: #333333;
				line-height: 40rpx;
			}

			.unit {
				padding-top: 74rpx;
				font-size: 32rpx;
				font-weight: 500;
				color: #FEB042;
				line-height: 44rpx;
			}

			image {
				width: 160rpx;
				height: 160rpx;
				border-radius: 10rpx;
				margin-right: 20rpx;
			}
		}
	}

	.bottom-btn {
		display: flex;
		justify-content: flex-end;
		padding-top: 20rpx;

		.cancel,
		.save {
			width: 152rpx;
			height: 58rpx;
			border-radius: 29rpx;
			font-size: 24rpx;
			font-weight: 400;
			line-height: 58rpx;
			text-align: center;
		}

		.cancel {
			border: 2rpx solid #666666;
			color: #666666;
			margin-right: 20rpx;
		}

		.save {
			border: 2rpx solid #00D08F;
			color: #00D08F;
		}
	}
</style>
